/* -------------------------------------------------------
   ğŸ”¹ Image â†’ PDF Converter Component
------------------------------------------------------- */

function ImageToPDFConverter() {
  const [images, setImages] = useState<File[]>([]);
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  // Handle image upload
  const handleUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return;
    setImages(Array.from(e.target.files));
  };

  // Convert images to PDF
  const handleConvert = async () => {
    if (images.length === 0) return;

    setLoading(true);

    const { jsPDF } = await import("jspdf");
    const pdf = new jsPDF();

    for (let i = 0; i < images.length; i++) {
      const img = images[i];

      const reader = new FileReader();
      reader.readAsDataURL(img);

      await new Promise<void>((resolve) => {
        reader.onload = () => {
          const imgData = reader.result as string;

          if (i > 0) pdf.addPage();
          pdf.addImage(imgData, "JPEG", 10, 10, 190, 250);

          resolve();
        };
      });
    }

    const pdfBlob = pdf.output("blob");
    const url = URL.createObjectURL(pdfBlob);
    setPdfUrl(url);
    setLoading(false);
  };

  return (
    <div className="bg-white/10 backdrop-blur-xl p-8 rounded-2xl border border-white/20 shadow-xl space-y-6">
      <h2 className="text-2xl font-semibold text-white">ØªØ¨Ø¯ÛŒÙ„ Ø¹Ú©Ø³ Ø¨Ù‡ PDF</h2>

      {/* Upload */}
      <input
        type="file"
        multiple
        accept="image/*"
        onChange={handleUpload}
        className="text-gray-200 block"
      />

      {images.length > 0 && (
        <p className="text-gray-300 text-sm">
          {images.length} ØªØµÙˆÛŒØ± Ø§Ù†ØªØ®Ø§Ø¨ Ø´Ø¯Ù‡
        </p>
      )}

      {/* Convert Button */}
      <button
        onClick={handleConvert}
        disabled={loading || images.length === 0}
        className="w-full py-3 bg-purple-600 hover:bg-purple-700 transition rounded-xl text-white font-semibold disabled:bg-gray-600"
      >
        {loading ? "Ø¯Ø± Ø­Ø§Ù„ ØªØ¨Ø¯ÛŒÙ„..." : "ØªØ¨Ø¯ÛŒÙ„ Ø¨Ù‡ PDF"}
      </button>

      {/* Download */}
      {pdfUrl && (
        <a
          href={pdfUrl}
          download="converted.pdf"
          className="block text-center w-full py-3 bg-green-600 hover:bg-green-700 transition rounded-xl text-white font-semibold"
        >
          Ø¯Ø§Ù†Ù„ÙˆØ¯ PDF
        </a>
      )}
    </div>
  );
}
